import { useState, useRef } from 'react';
import classnames from 'classnames';
import { Slider } from 'antd';

const setVolume = (value, id) => {
  let audio = document.getElementById(id);
  audio.volume = value;
};

const Ambience = (props) => {
  const [enable, setEnable] = useState(false);
  const audioRef = useRef();

  return (
    <>
      <audio
        id={props.name}
        ref={audioRef}
        src={props.src}
        loop
        muted={!enable}
        autoPlay
      />
      <div
        className={`flex items-center pt-8 cursor-pointer select-none ${
          enable ? 'text-secondary' : 'text-grey'
        }`}
        onClick={() => {
          setEnable(!enable);
        }}
      >
        <props.icon className={classnames(props.iconClass)} />
        {props.name}
        <div className="ml-auto">{`${audioRef.current?.volume * 100}%`}</div>
      </div>
      <Slider
        disabled={!enable}
        defaultValue={100}
        onChange={(value) => setVolume(value / 100, props.name)}
      ></Slider>
    </>
  );
};

export default Ambience;
